@import "https://fonts.googleapis.com/css2?family=M+PLUS+1p:wght@400;500;700;800;900&family=Noticia+Text:ital,wght@1,400;1,700&display=swap";
:is(*) {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul, li {
  list-style: none;
}

body {
  flex-direction: column;
  min-height: 100vh;
  display: flex;
}

* a {
  color: inherit;
  text-decoration: none;
}

@keyframes scroll-info {
  0% {
    transform: translateY(0);
  }

  15% {
    transform: translateY(3px);
  }

  30% {
    transform: translateY(0);
  }

  45% {
    transform: translateY(3px);
  }

  60% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes animate-next-btn {
  0%, 30%, 60%, 100% {
    translate: 0;
  }

  15%, 45% {
    translate: 3px;
  }
}

@keyframes animate-prev-btn {
  0%, 30%, 60%, 100% {
    translate: 0;
  }

  15%, 45% {
    translate: -3px;
  }
}

body {
